import * as React from "react";
import ClassNames from "classnames";
import { getCharColor } from "utils/ColorKit";
const styles = require("./index.less");
export default class CharAvatar extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { className, text } = this.props;
        return (React.createElement("span", { className: ClassNames([className], [styles.charAvatar]), style: { backgroundColor: getCharColor(text[0]) } }, text[0].toUpperCase()));
    }
}



